export default {
	name: 'UniCard',
	props: {
		title: {
			type: String,
			default: ''
		}, // 标题
		extra: {
			type: String,
			default: ''
		}, // 扩展信息
		note: {
			type: String,
			default: ''
		}, // Tips
		thumbnail: {
			type: String,
			default: ''
		}, // 缩略图
		// 卡片模式 ， 可选值 basic：基础卡片 ；style ：图文卡片 ； title ：标题卡片
		mode: {
			type: String,
			default: 'basic'
		},
		isFull: {
			// 内容区域是否通栏
			type: Boolean,
			default: false
		},
		isShadow: {
			// 是否开启阴影
			type: Boolean,
			default: false
		},
		backgroundColor: {
			// 是否开启阴影
			type: String,
			default: ''
		},
	},
	methods: {
		onClick() {
			this.$emit('click')
		}
	}
}
